import React, {useState} from 'react';
import './App.css';
import RequestForm from "./RequestForm";
import PrivateKeyForm from "./PrivateKeyForm";

function App() {

    const defaultKey=localStorage.getItem("signingDefaultKey")??""
    const defaultKeyId=localStorage.getItem("signingDefaultKeyId")??""

    const [privateKey, setPrivateKey]=useState(defaultKey)
    const [keyId, setKeyId]=useState(defaultKeyId)

    return (
        <div className="App">
            <RequestForm onSubmit={(s)=>{}} privateKey={privateKey} keyId={keyId}/>
            <hr/>
            <PrivateKeyForm privateKey={privateKey} keyId={keyId} onKeyChanged={setPrivateKey} onKeyIdChanged={setKeyId}/>
        </div>
    );
}

export default App;
