import React, {useState} from 'react';
import './App.css';
import './PrivateKeyForm.css'
import show from "./show.png"
import hide from "./hide.png"

type PrivateKeyFormProps = {
    privateKey: string,
    keyId: string,
    onKeyChanged: (txt: string) => void,
    onKeyIdChanged: (txt: string) => void
}

function PrivateKeyForm(props: PrivateKeyFormProps) {
    const [hidden,setHidden]=useState(true)

    return (
        <div>
            <h3>Creds</h3>
            <div className={"privateKeyForm"}>
                <div>
                    <span>Private key</span> <span onClick={()=>setHidden(!hidden)}>{hidden ? <img src={show} width={24}/> : <img src={hide} width={24}/>}</span>
                </div>

                <textarea rows={8} cols={80} className={(hidden?"hidden":"")} value={props.privateKey} onChange={(e) => props.onKeyChanged(e.target.value as string)}/>
                <br/>

            </div>
            <br/>
            <div className={"keyIdForm"}>
                <div>Key id</div>
                <input size={60} type={"text"} onChange={e=>props.onKeyIdChanged(e.target.value)} value={props.keyId}/>
            </div>
        </div>
    );
}

export default PrivateKeyForm;
