import React, {useState} from 'react';
import './App.css';
import http from "http";
import * as https from "https";
import "./RequestForm.css"

const crypto = require('crypto');

const httpSignature = require('http-signature')


type RequestFormProps = {
    onSubmit: (txt: string) => void,
    privateKey: string,
    keyId: string
}

const defaultRequest = 'GET http://www.acme.com/stuff\nAccept: application/json\n'

function RequestForm(props: RequestFormProps) {

    const [requestText, setRequestText] = useState(defaultRequest)
    const [signedRequestText, setSignedRequestText] = useState("")
    const [statusText, setStatusText] = useState("")
    const [generateDigest, setGenerateDigest] = useState(true)

    const splitHeader = (line: string): [string, string] => {
        const split: number = line.trim().indexOf(": ")
        return [line.substring(0, split), line.substring(split + 2)]
    }

    const calculateDigestHeader = (body: string) => {
        const hash = crypto.createHash('sha256').update(body).digest('base64');
        return `SHA-256=${hash}`;
    }

    const addSignature = (requestText: string, privateKey: string, keyId: string) => {
        const lines = requestText.split("\n")
        const verbLine = lines[0]

        const split = lines.findIndex(line => line.trim().length === 0)
        const headerLines = split === -1 ? lines.slice(1) : lines.slice(1, split)
        const body = split === -1 ? null : lines.slice(split + 1).join("\n")
        const digest = body === null || body.trim().length === 0 || !generateDigest ? null : calculateDigestHeader(body)

        const [method, urlString] = verbLine.split(" ")
        const url = new URL(urlString)

        const headerPairs: [string, string][] = headerLines
            .map(line => line.trim())
            .filter(line => line.length > 0)
            .map(line => splitHeader(line))
            .filter(line => line[0] !== 'Digest')
        if (digest !== null) {
            headerPairs.push(["Digest", digest])
        }

        const headers: any = {}
        headerPairs.forEach(p => {
            headers[p[0]] = p[1]
        })
        if (headers['Signature'] !== undefined) {
            throw "Request already signed!"
        }

        console.log({headers, body})


        const client = url.protocol === 'https:' ? https : http

        const options = {
            protocol: url.protocol,
            hostname: url.hostname,
            port: url.port,
            path: url.pathname + url.search,
            method,
            headers,
            body
        }


        const request = client.request(options, (req) => {
        })

        request.method = options.method
        request.path = options.path

        httpSignature.sign(request, {
            key: privateKey,
            keyId: keyId,
            headers: [...Object.keys(options.headers), '(request-target)'].filter(header => header.toUpperCase() !== 'COOKIE')
        })
        const signValue = (request.getHeader('Authorization') as string).substring('Signature '.length)

        const signedRequest: string[] = []
        signedRequest.push(request.method + " " + options.path)

        const headerKeys = Object.keys((request as any)._headers)
        headerKeys.map(key => (request as any)._headers[key] as any)
            .forEach(headerObj => signedRequest.push(headerObj.name + ": " + headerObj.value))

        // Log request
        console.log("=============================================")
        console.log(signedRequest.join("\n"))
        console.log("---------------------------------------------")

        localStorage.setItem("signingDefaultKey", privateKey)
        localStorage.setItem("signingDefaultKeyId", keyId)

        return (method + " " + urlString + "\n" + headerPairs.map(hp => hp[0] + ": " + hp[1]).join("\n") + "\nSignature: " + signValue + "\n\n" + body)
    }

    const onChangeRequest = (e: any) => {
        setRequestText(e.target.value as string)
        setSignedRequestText("")
    }

    const onSubmit = (requestText: string): void => {
        try {
            setSignedRequestText(addSignature(requestText, props.privateKey, props.keyId))
            setStatusText("ok")
        } catch (error) {
            setStatusText("" + error)
        }
    }

    return (
        <div>
            <div className="requestform">
            <textarea rows={30} cols={150} value={requestText}
                      onChange={onChangeRequest}/>
                <div>{"==>"}</div>
                <textarea rows={30} cols={150} value={signedRequestText} readOnly={true}/>
            </div>
            <br/>
            <label>
                <input type={"checkbox"} onChange={(e) => setGenerateDigest(e.target.checked)}
                       checked={generateDigest}></input>
                Generate digest
            </label>
            <br/>
            <button onClick={(e) => onSubmit(requestText)}>Sign</button>
            <br/>
            <span>{statusText}</span>
        </div>
    );
}

export default RequestForm;
